<template>
  <div class="adder-box">
    <input type="text" placeholder="请输入你的任务名称，按回车键确认" v-model="title" @keyup.enter="add">
  </div>
</template>

<script>

export default {
  name: "AdderBox",
  data() {
    return {
      title:""
    }
  },
  methods: {
    add(){
      if (this.title.trim() === "") {
        return alert("输入不能为空")
      }
      this.receive(this.title);
      this.title = ""
    }
  },
  props:["receive"]
}
</script>

<style scoped>
  .adder-box input {
    width: 560px;
    height: 28px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 4px 7px;
  }
  .adder-box input:focus {
    outline: none;
    border-color: rgba(82, 168, 236, 0.8);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(82, 168, 236, 0.6);
  }
</style>
